import React, {FC} from 'react'
import Paper from '@material-ui/core/Paper'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import Typography from '@material-ui/core/Typography'
import Divider from '@material-ui/core/Divider'

type MenuContentProps = {
    custom: any;
    data: any;
}

const MenuContent: FC<MenuContentProps> = ({ custom, data }) => {
    const menuContent = data.menuItems.map((item: any) => (
        <ListItem
            key={item.title}
            button
            className={custom.menuItem}
            onClick={() => console.log("click side bar")}
            >
            <ListItemIcon>{item.icon}</ListItemIcon>
            <ListItemText primary={item.title} />

        </ListItem>
    ))
    
    const customContent = data.customization.map((item: any) => (
        <ListItem
            key={item.title}
            button
            className={custom.menuItem}
            onClick={() => console.log("click side bar")}
        >
            <ListItemIcon>{item.icon}</ListItemIcon>
            <ListItemText primary={item.title} />
        </ListItem>
    ))

    return (
        <Paper className={custom}>
            <Typography variant='h5' gutterBottom align='left'>
                {data.title.text}
            </Typography>
            <List>
                {menuContent}
            </List>
            <Divider />
            <List>
                {customContent}
            </List>
        </Paper>
    )
}

export default MenuContent
